/* 기본 스타일 초기화 및 공통 스타일 설정 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Nanum Gothic', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f9fa;
  color: #333;
  min-height: 100vh;
  font-size: 16px;
  line-height: 1.5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
}

/* 앱 전체에 적용되는 공통 요소 */
h1, h2, h3, h4, h5, h6 {
  color: #212529;
  line-height: 1.3;
  margin-bottom: 0.5em;
}

p {
  margin-bottom: 1rem;
}

button {
  cursor: pointer;
  font-family: inherit;
}

input, textarea, select, button {
  font-family: inherit;
}

/* 링크 스타일 */
a {
  color: #4263eb;
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  color: #3b5bdb;
  text-decoration: underline;
}

/* 아이콘에 적용되는 스타일 */
.icon {
  vertical-align: middle;
}

/* 스크롤바 스타일 */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f3f5;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #dee2e6;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #adb5bd;
}

/* 접근성 포커스 스타일 */
:focus-visible {
  outline: 3px solid rgba(66, 99, 235, 0.5);
  outline-offset: 2px;
}

/* 모바일 화면 최적화 */
@media (max-width: 768px) {
  body {
    font-size: 15px;
  }
  
  h1 {
    font-size: 1.8rem;
  }
  
  h2 {
    font-size: 1.5rem;
  }
  
  h3 {
    font-size: 1.3rem;
  }
}

/* 인쇄 스타일 최적화 */
@media print {
  body {
    font-size: 12pt;
    background: #fff;
    color: #000;
  }
  
  a {
    color: #000;
    text-decoration: underline;
  }
} 